<template>
  <div class="products-bg">
    <div class="bd-example">
      <div id="carouselExampleCaptions"
           class="carousel slide"
           data-ride="carousel">
        <ol class="carousel-indicators">
          <li data-target="#carouselExampleCaptions"
              data-slide-to="0"
              class="active"></li>
          <li data-target="#carouselExampleCaptions"
              data-slide-to="1"></li>
          <li data-target="#carouselExampleCaptions"
              data-slide-to="2"></li>
        </ol>
        <div class="carousel-inner">
          <div class="carousel-item active">
            <div class="bg-cover vh-100"
                 style="background-image:url('https://images.unsplash.com/photo-1464297162577-f5295c892194?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1050&q=80');
                 "></div>
            <div class="carousel-caption d-none d-md-block">
              <h5 class="h2"></h5>
              <p class="px-5"></p>
            </div>
          </div>
          <div class="carousel-item">
            <div class="bg-cover vh-100"
                 style="background-image:url('https://images.unsplash.com/photo-1423483641154-5411ec9c0ddf?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1050&q=80');
                 "></div>
            <div class="carousel-caption d-none d-md-block">
              <h5 class="h2"></h5>
              <p class="px-5"></p>
            </div>
          </div>
          <div class="carousel-item">
            <div class="bg-cover vh-100"
                 style="background-image:url('https://images.unsplash.com/photo-1544144683-bf1749166af3?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1050&q=80');
                 "></div>
            <div class="carousel-caption d-none d-md-block">
              <h5 class="h2"></h5>
              <p class="px-5"></p>
            </div>
          </div>
        </div>
        <a class="carousel-control-prev"
           href="#carouselExampleCaptions"
           role="button"
           data-slide="prev">
          <span class="carousel-control-prev-icon"
                aria-hidden="true"></span>
          <span class="sr-only">Previous</span>
        </a>
        <a class="carousel-control-next"
           href="#carouselExampleCaptions"
           role="button"
           data-slide="next">
          <span class="carousel-control-next-icon"
                aria-hidden="true"></span>
          <span class="sr-only">Next</span>
        </a>
      </div>
    </div>

    <section class="mt-5 l-scrool">
      <div class="container">
        <div class="row">
          <div class="col-lg-3">
            <div class="list-group sticky-top"
                 style="top:66px">
              <a href="#"
                 class="list-group-item list-group-item-action rounded-0"
                 :class="{'active':active==='all'}"
                 @click.prevent="choose('all')">
                <i class="fas fa-briefcase"></i>
                </a>
              <a href="#"
                 class="list-group-item list-group-item-action"
                 :class="{'active':active===''}"
                 @click.prevent="choose('')">
                <i class="fab fa-apple fa-lg"></i></a>
              <a href="#"
                 class="list-group-item list-group-item-action"
                 :class="{'active':active===''}"
                 @click.prevent="choose('')">
                <i class="fas fa-cocktail"></i></a>
            </div>
          </div>

          <div class="col-lg-9">
            <card :active='active'></card>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import card from '@/components/custom/card'
import alert from '@/components/common/alert'
export default {
  data () {
    return {
      active: 'all'
    }
  },
  components: {
    card,
    alert
  },
  methods: {
    choose (get) {
      this.active = get
    }
  }
}
</script>



