<template>
  <div>
    <div class="modal fade"
         id="couponModal"
         tabindex="-1"
         role="dialog"
         aria-labelledby="exampleModalLabel"
         aria-hidden="true">
      <div class="modal-dialog"
           role="document">
        <div class="modal-content">
          <div class="modal-header bg-warning text-white"
               v-if="tempCoupon.id">
            <h5 class="modal-title"
                id="exampleModalLabel"></h5>
            <button type="button"
                    class="close"
                    data-dismiss="modal"
                    aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>

          <div class="modal-header bg-primary text-white"
               v-else>
            <h5 class="modal-title"
                id="exampleModalLabel"></h5>
            <button type="button"
                    class="close"
                    data-dismiss="modal"
                    aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>

          <div class="modal-body">
            <div class="form-group">
              <label for="title"></label>
              <input type="text"
                     class="form-control"
                     id="title"
                     name="title"
                     v-model="tempCoupon.title"
                     v-validate="'required'"
                     placeholder="">
              <span class="text-danger"
                    v-if="errors.has('title')"></span>
            </div>
            <div class="form-group">
              <label for="coupon_code"></label>
              <input type="text"
                     class="form-control"
                     id="coupon_code"
                     name="coupon_code"
                     v-model="tempCoupon.code"
                     v-validate="'required'"
                     placeholder="">
              <span class="text-danger"
                    v-if="errors.has('coupon_code')"></span>
            </div>
            <div class="form-group">
              <label for="due_date"></label>
              <input type="date"
                     class="form-control"
                     id="due_date"
                     name="due_date"
                     v-validate="'required'"
                     v-model="due_date">
              <span class="text-danger"
                    v-if="errors.has('due_date')"></span>
            </div>
            <div class="form-group">
              <label for="price"></label>
              <input type="number"
                     class="form-control"
                     id="price"
                     name="price"
                     v-validate="'required'"
                     v-model="tempCoupon.percent"
                     placeholder="">
              <span class="text-danger"
                    v-if="errors.has('price')"></span>
            </div>
            <div class="form-group">
              <div class="form-check">
                <input class="form-check-input"
                       type="checkbox"
                       :true-value="1"
                       :false-value="0"
                       v-model="tempCoupon.is_enabled"
                       id="is_enabled">
                <label class="form-check-label"
                       for="is_enabled">
                  
                </label>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button"
                    class="btn btn-outline-secondary"
                    data-dismiss="modal"></button>
            <button type="button"
                    class="btn btn-outline-primary"
                    @click="updateCoupons()"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: ['tempCoupon'],
  data () {
    return {
      due_date: ''
    }
  },
  watch: {
    tempCoupon () {
      this.errors.clear()
    }
  },
  methods: {
    updateCoupons () {
      this.$validator.validate().then((valid) => {
        if (valid) {
          this.$emit('updateCoupons', this.due_date)
          this.due_date = ''
        }
      })
    }
  }
}
</script>



